.contributors {
  margin-top: 46px;
  padding-bottom: 100px;
  background-image: url('../../img/main-backgroung.png');
  background-repeat: no-repeat;
  background-position: 70% 110%;
  background-size: auto;
  background-color: $transparent;
}

.contributors__title {
  position: relative;
  margin-bottom: 33px;
  letter-spacing: -0.9px;
  padding-bottom: 31px;
  font-size: 21px;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, #00132E 0%, rgba(0, 19, 46, 0) 60%);
  }
}

.contributors__list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 60px;

  &:last-child {
    margin-bottom: 0;
  }
}

.contributors__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contributors__name {
  padding-right: 350px;
  width: 100%;
  position: relative;
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  letter-spacing: -0.9px;
  cursor: pointer;
  text-decoration: none;
  color: $main-text;
  opacity: 0.5;
  transition: opacity 0.5s ease;

  &:hover {
    opacity: 1;
  }

  &:focus {
    opacity: 1;
  }

  &::after {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    width: 20px;
    height: 20px;
    transform: translateY(-50%);
    padding-right: 480px;

    background-image: url('../../img/arrow.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }
}

.contributors__logo {
  margin-right: 200px;
}

.contributors__image {
  width: 150px;
  height: auto;
}