.about {
  position: relative;
  display: grid;
  grid-template-columns: 1fr auto;
  margin-bottom: 84px;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, #00132E 0%, rgba(0, 19, 46, 0) 80%);
  }

  &:first-child {
    margin-top: 114px;
  }

  &:last-child {
    margin-bottom: 242px;
  }
}

.about__description {
  display: flex;
  flex-direction: column;
}

.about__images {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 50px;
}

.about__title {
  font-size: 36px;
  line-height: 41px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 24px;
}

.about__text {
  margin: 0;
  margin-bottom: 114px;

  &--big {
    margin-bottom: 50px;
  }
}

.about__quote {
  font-style: italic;
  margin: 0;
  margin-bottom: 97px;

  &--big {
    margin-bottom: 78px;
  }
}

.about__author {
  margin: 0;
  font-weight: 700;
  margin-bottom: 35px;
  letter-spacing: -0.5px;
}

.about__image {
  margin-bottom: 36px;
  box-shadow: 8px 8px 4px rgba(0, 0, 0, 0.15);
}

.about-us {
  position: relative;
  margin-top: 90px;
  padding-bottom: 42px;
}

.about-us__title {
  position: relative;
  margin-bottom: 33px;
  letter-spacing: -0.9px;
  padding-bottom: 31px;
  font-size: 28px;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, #00132E 0%, rgba(0, 19, 46, 0) 60%);
  }
}

.about-us__text {
  font-size: 18px;
  line-height: 28px;
}

.about-us__contacts {
  margin: 0 auto;
  margin-top: 200px;
  width: 700px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-weight: 700;
  font-size: 18px;
  line-height: 28px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: linear-gradient(90deg, #00132E 0%, rgba(0, 19, 46, 0) 100%);
  }
}

.about-us__contact {
  padding: 10px 30px;
  text-decoration: none;
  color: $main-text;
  display: flex;
  align-items: center;

  &:hover {
    color: $blue;
  }

  &:active {
    color: $blue;
  }

  &:focus {
    color: $blue;
  }
}

.about-us__svg-top {
  margin-right: 13px;
}