.timeline {
  position: relative;
  margin-top: 90px;
  padding-bottom: 42px;

  &::after {
    z-index: -1;
    content: "";
    position: absolute;
    bottom: -150px;
    right: 70px;
    width: 498px;
    height: 1056px;
    background-image: url('../../img/main-backgroung.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;
    background-color: $transparent;
  }
}

.timeline__list {
  padding: 0;
}

.timeline__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.timeline__description {
  padding-right: 150px;
}

.timeline__date {
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  margin: 0;
}

.timeline__text {
  font-size: 16px;
  line-height: 20px;
  margin-top: 0;
  font-weight: 500;
}

.timeline__link {
  text-decoration: none;
  color: $main-text;
  
  &:hover {
    color: $blue;
  }
}

.timeline__image {
  width: 200px;
  height: auto;
}