.technology {
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr;
  margin-top: 90px;
  padding-bottom: 42px;

  &::after {
    z-index: -1;
    content: "";
    position: absolute;
    bottom: -150px;
    right: 70px;
    width: 498px;
    height: 1056px;
    background-image: url('../../img/main-backgroung.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;
    background-color: $transparent;
  }
}

.technology__nav {
  max-width: 500px;
}

.technology__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-right: 48px;
}

.technology__item {
  cursor: pointer;
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  margin-bottom: 42px;
  padding-right: 120px;
  letter-spacing: -1.2px;
}

.technology__link {
  position: relative;
  display: inline-block;
  width: 100%;
  box-sizing: content-box;
  opacity: 0.5;
  transition: opacity 0.5s ease;

  &:hover {
    opacity: 0.8;
  }

  &::after {
    content: '';
    position: absolute;
    right: -100px;
    top: 50%;
    width: 20px;
    height: 26px;
    transform: translateY(-50%);

    background-image: url('../../img/arrow.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }

  &--active {
    opacity: 1;
  }
}

.description_show {
  display: none;
}

.description__title {
  display: flex;
  flex-direction: column;
  position: relative;
  font-size: 22px;
  line-height: 32px;
  font-weight: 700;
  letter-spacing: -1.2px;
  margin: 0;
  padding-bottom: 28px;

  &--row {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  &::after {
    content: "";
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, #00132E 0%, rgba(0, 19, 46, 0) 90%);
  }
}

.description__title-span {
  margin-bottom: 14px;
  width: 100%;
}

.description__text {
  font-size: 16px;
  line-height: 22px;
  margin-top: 24px;
  margin-bottom: 22px;
  letter-spacing: -0.5px;
}

.description__image {
  margin-bottom: 14px;

  &--second {
    width: 670px;
    height: auto;
  }

  &--third {
    width: 220px;
    height: auto;
    margin-bottom: 0;
  }

  &--fourth {
    width: 200px;
    height: auto;
    margin-bottom: 0;
  }
}

.description__title-list {
  line-height: 25px;
  letter-spacing: -0.5px;
}

.description__list {
  margin: 0;
  margin-bottom: 30px;
}

.description__item {
  line-height: 25px;
  letter-spacing: -0.5px;
}